import React from 'react'
import {Card, Divider, Select, Steps, Button, Form,Icon,BackTop,Upload,message,Table,Collapse,Switch,Slider,Tabs,notification} from 'antd'
import {inject,observer} from 'mobx-react'
import axios from 'axios'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import './css/formDeni2.css'
import TypingCard from '../../../components/TypingCard'

const Dragger = Upload.Dragger;
const Panel = Collapse.Panel
const { Step } = Steps;
let projectid='';

//用户自定义约简比例
const marks = {
  0: "0",
  100: {
      style: {
          color: "#f50"
      },
      label: <strong>100%</strong>
  }
};

//变异体信息
const columnsmutInfo = [
  {
    title: 'Name',
    dataIndex: 'name',
    sorter: true,
    render: name => `${name.first} ${name.last}`,
    width: '20%',
  }, {
    title: 'Gender',
    dataIndex: 'gender',
    filters: [
      {text: 'Male', value: 'male'},
      {text: 'Female', value: 'female'},
    ],
    width: '20%',
  }, {
    title: 'Email',
    dataIndex: 'email',
  }]

  //覆盖测试报告
const columnscoveredInfo = [
  {
    title: 'Name',
    dataIndex: 'name',
    sorter: true,
    render: name => `${name.first} ${name.last}`,
    width: '20%',
  }, {
    title: 'Gender',
    dataIndex: 'gender',
    filters: [
      {text: 'Male', value: 'male'},
      {text: 'Female', value: 'female'},
    ],
    width: '20%',
  }, {
    title: 'Email',
    dataIndex: 'email',
  }]

  //变异测试报告
const columnsmuttestInfo = [
  {
    title: '变异体数目',
    dataIndex: 'numofmutantre',
  }, {
    title: '测试用例数目',
    dataIndex: 'numofts',
  }, {
    title: '变异得分',
    dataIndex: 'mscore',
  }]

// 文件上传
// 弄清楚文件上传逻辑，保存文件至服务器
const propsfileupload = {
  name: 'file',
  multiple: true,
  action: '//jsonplaceholder.typicode.com/posts/',
  onChange(info) {
    const status = info.file.status;
    if (status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    if (status === 'done') {
      projectid=info.file.name;
      message.success(`${info.file.name} file uploaded successfully.`);
    } else if (status === 'error') {
      message.error(`${info.file.name} file upload failed.`);
    }
  },
}


@inject('stepFormStore') @Form.create() @observer
class Step1 extends React.Component{
  state={
    submitloading:false
  }
  // 传入项目id至后端，触发变异生成，生成变异结果，返回变异体总数
  nextStep = () =>{
    this.setState({
      submitloading:true
    })
    setTimeout(()=>{
     this.setState({
       submitloading:false
    })
    this.props.stepFormStore.setNumofmutant(1200)
    this.props.stepFormStore.setCurrent(1)
    console.log(projectid)
   },2000)
  }
  render(){
    return (
      <div>
        <Card bordered={false} className='card-item' title=''>
              <Dragger {...propsfileupload}>
                <p className="ant-upload-drag-icon">
                  <Icon type="inbox"/>
                </p>
                <p className="ant-upload-text">点击或拖拽到此处完成上传，请上传zip文件</p>
              </Dragger>
        </Card>
        <div id="step3"><Button type='primary' onClick={this.nextStep} loading={this.state.submitloading}>变异生成</Button></div>     
        <Divider/>
        <div className='desc'>
          <h3>说明</h3>
          <h4>变异生成</h4>
          <p>
            在进行变异体集合约简之前，需要为项目生成变异体。请上传项目前先配置pitest插件，具体操作详见首页。点击“变异生成”按钮后会显示变异体集合的信息。
          </p>
        </div>
        <BackTop visibilityHeight={200} style={{right: 50}}/>
      </div>
    )
  }
}

@inject('stepFormStore') @Form.create() @observer
class Step2 extends React.Component{
  state = {
    loading:false,
    submitloading:false,
    pagination: {
      pageSize: 8
    },
    datamutinfo:[],
    isreduced:false
  }
  // 变异结果以生成，传入项目id,返回结果列表，注意搞清分页逻辑
  componentDidMount() {
    this.getRemoteData()
  }

  getRemoteData(params) {
    this.setState({
      loading: true
    })
    axios.get('https://randomuser.me/api', {
      params: {
        results: 10,
        size: 200,
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = 200
      this.setState({
        loading: false,
        datamutinfo: res.data.results,
        pagination
      })
    })
  }

  handleTableChange = (pagination, filters, sorter) => {
    const pager = {...this.state.pagination};
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getRemoteData({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters,
    })
  }
// 传入项目id，触发变异约简，生成变异约简结果，生成约简后项目，变异得分，测试用例数
  handleSubmit = ()=>{
    this.props.form.validateFields((err,values)=>{
      if (!err){
        this.setState({
          submitloading:true
        })
       setTimeout(()=>{
         this.setState({
           submitloading:false
         })
         this.props.stepFormStore.setNumofmutantre(710)
         this.props.stepFormStore.setNumofts(110)
         this.props.stepFormStore.setMscore(0.91)
         this.props.stepFormStore.setCurrent(2)
       },2000)
      }
    })
  }

  onChange = value => {
    console.log("222=>", value);
  };

  render(){
    return (
      <div>
        <Card bordered={false} title='变异体集合信息' style={{marginBottom: 10}} id='remoteLoading'>
          <Collapse defaultActiveKey={['1']}>
                <Panel header={`变异体总数：${this.props.stepFormStore.numofmutant}`} key="1">
                    <Table rowKey={record => record.login.uuid}
                    loading={this.state.loading}
                    dataSource={this.state.datamutinfo}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    columns={columnsmutInfo} style={styles.tableStyle}/>
                </Panel>
          </Collapse>  
        </Card>
        <Card bordered={false} title='确认约简信息' id='remoteLoading'>
        是否自定义约简比例：<Switch onChange={(checked) => this.setState({isreduced: checked})}/>
        {this.state.isreduced?
          <Slider
          marks={marks}
          range
          onChange={this.onChange}
          defaultValue={[20,50]}
          />
        :''}
        <div id="step3">
            <Button type="primary" onClick={this.handleSubmit} loading={this.state.submitloading}>提交</Button>
            <Button onClick={()=>this.props.stepFormStore.setCurrent(0)} style={{ marginLeft: 8 }}>上一步</Button>
        </div>
        </Card>
        
      </div>
    )
  }
}

@inject('stepFormStore') @observer
class Step3 extends React.Component{
  state = {
    loading:false,
    submitloading:false,
    pagination: {
      pageSize: 8
    },
    datamutinfo:[],
  }
  componentDidMount() {
    this.getRemoteData()
  }
//输入项目id 传回约简结果
  getRemoteData(params) {
    this.setState({
      loading: true
    })
    axios.get('https://randomuser.me/api', {
      params: {
        results: 10,
        size: 200,
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = 200
      this.setState({
        loading: false,
        datamutinfo: res.data.results,
        pagination
      })
    })
  }

  handleTableChange = (pagination, filters, sorter) => {
    const pager = {...this.state.pagination};
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getRemoteData({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters,
    })
  }

  handleSubmit = ()=>{
    this.props.form.validateFields((err,values)=>{
      if (!err){
        this.setState({
          loading:true
        })
       setTimeout(()=>{
         this.setState({
           loading:false
         })
         this.props.stepFormStore.setCurrent(3)
       },2000)
      }
    })
  }

  openNotificationType=(type)=>{
    notification[type]({
      message: 'Notification Title',
      description: 'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
    });
  }

  render(){
    return (
        <div>
          <div className='icon-box'>
            <Icon type='check-circle'/>
          </div>
          <div>
            <h3 className='success'>操作成功</h3>
          </div>
          <Card bordered={false} title='约简后变异体集合信息' style={{marginBottom: 10}} id='remoteLoading'>
            <Collapse defaultActiveKey={['1']}>
                  <Panel header={`变异体总数：${this.props.stepFormStore.numofmutantre}`} key="1">
                      <Table rowKey={record => record.login.uuid}
                      loading={this.state.loading}
                      dataSource={this.state.datamutinfo}
                      pagination={this.state.pagination}
                      onChange={this.handleTableChange}
                      columns={columnsmutInfo} style={styles.tableStyle}/>
                  </Panel>
            </Collapse>  
          </Card>
          <div id='step3'>
            <Button type='primary' onClick={()=>this.props.stepFormStore.setCurrent(3)}>查看测试报告</Button>
            {/* 输出项目id和用户名，触发保存模型 */}
            <Button style={{ marginLeft: 8 }} onClick={()=>this.openNotificationType('success')}>保存模型</Button>
            <Button onClick={()=>this.props.stepFormStore.setCurrent(0)} style={{ marginLeft: 8 }}>再次约简</Button>
          </div>
        </div>
    )
  }
}

@inject('stepFormStore') @observer
class Step4 extends React.Component{
  state = {
    loading:false,
    submitloading:false,
    pagination: {
      pageSize: 8
    },
    datacoveredinfo:[],
    datamuttestinfo:[]
  }
// 传入项目id，进行覆盖测试，传回结果
  getRemoteCoveredData(params) {
    this.setState({
      loading: true
    })
    axios.get('https://randomuser.me/api', {
      params: {
        results: 10,
        size: 200,
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = 200
      this.setState({
        loading: false,
        datacoveredinfo: res.data.results,
        pagination
      })
    })
  }
// 将结果写入
  getRemoteMuttestData(params) {
    this.setState({
      loading: true
    })
    const mockdata = [
      {
        key: '1',
        numofmutantre:this.props.stepFormStore.numofmutantre,
        numofts:this.props.stepFormStore.numofts,
        mscore:this.props.stepFormStore.mscore
      }
    ]
    setTimeout(()=>{
      this.setState({
        loading:false,
        datamuttestinfo:mockdata
      })
    },2000)
  }

  handleTableChange = (pagination, filters, sorter) => {
    const pager = {...this.state.pagination};
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getRemoteData({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters,
    })
  }

  handleSubmit1 = ()=>{
    this.getRemoteCoveredData()
  }

  handleSubmit2 = ()=>{
    this.getRemoteMuttestData()
  }

  render(){
    return (
      <Card bordered={false} className='card-item' title='测试报告'>
        <Tabs defaultActiveKey="1">
          <Tabs.TabPane tab='覆盖测试' key='1'>
          <Button type="primary" style={{marginRight:20}} onClick={this.handleSubmit1} loading={this.state.loading}>生成覆盖测试报告</Button>
          <Card bordered={false} title='覆盖测试报告' style={{marginBottom: 10}} id='remoteLoading'>
            <Table rowKey={record => record.login.uuid}
            loading={this.state.loading}
            dataSource={this.state.datacoveredinfo}
            pagination={this.state.pagination}
            onChange={this.handleTableChange}
            columns={columnscoveredInfo} style={styles.tableStyle}/>     
          </Card>
          </Tabs.TabPane>
          <Tabs.TabPane tab='变异测试' key='2'>
          <Button type="primary" style={{marginRight:20}} onClick={this.handleSubmit2} loading={this.state.loading}>生成变异测试报告</Button>
          <Card bordered={false} title='变异测试报告' style={{marginBottom: 10}} id='basicUsage'>
          <Table loading={this.state.loading} dataSource={this.state.datamuttestinfo} columns={columnsmuttestInfo} style={styles.tableStyle}/>
        </Card>
          </Tabs.TabPane>
        </Tabs>
      </Card>
    );
  }

}

@inject('stepFormStore') @observer
class FormDemo2 extends React.Component {
  showStep = ()=>{
    switch (this.props.stepFormStore.current){
      case 1 : return <Step2/>
      case 2: return <Step3/>
      case 3: return <Step4/>
      default: return <Step1/>
    }
  }
  render () {
    return (
      <div>
        <CustomBreadcrumb arr={['变异体集合约简', '新建']}/>
        <TypingCard source='对一个庞大的变异体集合进行约简'/>
        <Card title='变异体集合约简' bordered={false} style={{minHeight: 600}}>
          <Steps style={styles.steps} current={this.props.stepFormStore.current}>
            <Step title="上传项目" />
            <Step title="变异体集合约简" />
            <Step title="约简结果" />
            <Step title="测试报告"></Step>
          </Steps>
          <div>{this.showStep()}</div>
        </Card>
      </div>
    )
  }
}

const styles = {
  steps:{
    maxWidth:750,
    margin: '16px auto'
  },
  desc:{
    padding:'0 56px',
  }
}

export default FormDemo2


